import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/pages/layout'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/manage/index',
      name: 'Layout',
      component: () => import('@/pages/layout')
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('@/pages/login/index')
    },
    {
      path: '/guide',
      name: 'Guide',
      component: () => import('@/pages/guide')
    },
    {
      path: '/findpwd',
      name: 'Findpwd',
      component: () => import('@/pages/login/findPwd')
    },
    {
      path: '/manage',
      name: 'Manage',
      redirect: '/manage/index',
      component: Layout,
      children: [
        {
          path: 'index',
          name: 'ManageIndex',
          component: () => import('@/pages/manage/index')
        }
      ]
    },
    {
      path: '/manage/add',
      name: 'ManageAdd',
      component: () => import('@/pages/manage/add')
    },
    {
      path: '/manage/detail',
      name: 'ManageDetail',
      component: () => import('@/pages/manage/detail')
    },
    {
      path: '/meeting',
      name: 'Meeting',
      redirect: '/meeting/index',
      component: Layout,
      children: [
        {
          path: 'index',
          name: 'MeetingIndex',
          component: () => import('@/pages/meeting/index')
        }
      ]
    },
    {
      path: '/address',
      name: 'Address',
      redirect: '/address/list',
      component: Layout,
      children: [
        {
          path: 'list',
          name: 'AddressList',
          component: () => import('@/pages/addressList/list')
        }
      ]
    },
    {
      path: '/me',
      name: 'Me',
      redirect: '/me/index',
      component: Layout,
      children: [
        {
          path: 'index',
          name: 'MeIndex',
          component: () => import('@/pages/me/index')
        }
      ]
    },
    {
      path: '/me/detail',
      name: 'Medetail',
      component: () => import('@/pages/me/info/detail')
    },
    {
      path: '/me/edit-pwd',
      name: 'MeEditPwd',
      component: () => import('@/pages/me/settings/editPwd')
    },
    {
      path: '/me/about',
      name: 'MeAbout',
      component: () => import('@/pages/me/about/index')
    },
    {
      path: '/me/about/service',
      name: 'MeAboutService',
      component: () => import('@/pages/me/about/customer-service')
    },
    {
      path: '/me/about/share',
      name: 'MeAboutShare',
      component: () => import('@/pages/me/about/share')
    }
  ]
})



