<template>
  <div>
    <transition name="fade-transform" mode="out-in">
      <router-view/>
    </transition>
    <van-tabbar class="cme-tabbar" route>
      <van-tabbar-item to="/manage/index">
        <van-icon class="iconfont" class-prefix='icon' slot="icon" name='huiyiricheng'></van-icon>
        <span>会议管理</span>
      </van-tabbar-item>
      <van-tabbar-item to="/meeting/index"  icon="phone">
        <van-icon  class="iconfont" class-prefix='icon' slot="icon" name='shipin' size="0.8rem"></van-icon>
        <span>视频会议</span>
      </van-tabbar-item>
      <van-tabbar-item to="/address/list" icon="friends">通讯录</van-tabbar-item>
      <van-tabbar-item to="/me/index"  icon="manager">我</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Tabbar, TabbarItem, Icon } from 'vant'
export default {
  name: 'PageHome',
  components: {
    [Tabbar.name]: Tabbar,
    [Icon.name]: Icon,
    [TabbarItem.name]: TabbarItem
  },
  data () {
    return {
      icon: {

      }
    }
  }
}
</script>

<style scoped>
.cme-tabbar{
  height: 60px;
}
</style>



